html,
body {
    overflow-x:hidden
}

body {
    margin: 0;
}

.background {
    padding: 1px 0;
    background: #23252C url("../src/images/walper2.png");
    background-size: cover;
}

.app {
    width: 70%;
    background: #1A1B21;
    margin: 5% auto;
    border-radius: 15px;
    font-family: 'Neue Machina', 'DM Sans', sans-serif;
}

nav {
    width: 10%;
    position: fixed;
    margin-left: 87.5%;
    margin-top: 5%;
    z-index: 10;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

nav img {
    width: 100%;
    margin-bottom: 10px;
}

h1 {
    font-size: 1.5rem;
    font-weight: 700;
    /*color: white;*/
    color: #d4d7c5;
    margin: 0 0 .125em 0;
    text-transform: uppercase;
}

h3,
h4 {
    /*color: #F5F5F5;*/
    color: #d4d7c5;
    font-size: 1rem;
    font-weight: 700;
    margin: 0 0 .9em 0;
    text-transform: uppercase;
}

p, li, table{
    /*color: #cecece;*/
    color: #b5b18b;
    font-size: 0.8rem;
    line-height: 1.2rem;
    margin: 0;
}

p {
    text-align: justify;
}

ul {
    margin: 0;
    padding-left: .9rem;
}

a {
    text-decoration: none;
    color: inherit;
}

/* navbar */

.navbar--icon {
    transform: scale(1);
    transition: transform 0.3s ease-in-out;
    max-width: 5rem;
}

.navbar--icon:hover {
    transform: scale(1.3);
    transition: transform 0.3s ease-in-out;
}

.navbar--theme--icon {
    filter: invert(100%);
}

.navbar--github--icon {
    transform: scale(0.95);
    filter: invert(95%);
}

.navbar--linkedin--icon {
    border-radius: 18%;
    transform: scale(0.9);
}

.navbar--mail--icon {

}


.avatar {
    width: 100%;
    display: block;
    border-radius: 15px 15px 0 0;
    margin-bottom: 1.3125em;
}

.main--info {
    text-align: center;
}

.occupation {
    font-size: .8125rem;
    font-weight: 400;
    color: #F3BF99;
    /*margin: 0 0 .6em 0;*/
    margin: 0 0 2em 0;
    text-transform: uppercase;
}

.buttons {
    display: flex;
    justify-content: space-between;
    padding: 0 10%;
    margin-bottom: 2em;
}

.buttons a {
    width: 45%;
}

.button {
    min-width: 100px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    padding: .6em 0;
    border: none;
    background: #d4d7c5;
    cursor: pointer;
    letter-spacing: .05rem;
    font-size: 0.8rem;
    font-weight: 500;
}

.cv--button {
    background: #f3bf99;
    /*color: white;*/
    color: black;
}

.anchor--icon {
    width: 1em;
    margin-right: 0.6em;
}

.resume--icon {
    filter: invert(0);
    width: .95em
}

.body {
    padding: 0 10%;
    margin: 1.7em 0;
}

.text--section {
    margin-bottom: 1.7rem;
}

.about--text {
    text-indent: 3em;
}

.education--history--container table {
    table-layout: fixed;
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 0.7em;
    margin-top: -.7em;
}

.spacer {
    height: .5em;
    border: black solid 1px;
}

table, td {
