}

table {
    width: 50%;
}

td:nth-child(1) {
    width: 100%;
}

td:nth-child(2) {
    width: 40%;
}

.education--name {
    font-weight: 700;
    color: #d4d7c5;
    display: flex;
    align-items: center;
    /*border: red solid 1px;*/
}

.education--years {
    text-align: right;
}

.education--logo {
    max-width: 2.5rem;
    margin-right: 1rem;
}

.education--link--logo {
    filter: invert(95%);
}

.education--main--info {
    /*padding-left: 3.5em;*/
    /*font-weight: 600;*/
    /*border: black 1px solid;*/
}

.education--text {
    /*padding-right: 4em;*/
    text-indent: 3.5em;
    text-align: justify;
}

.education--links {
    text-align: right;
    text-decoration: underline;
    /*padding-right: 1em;*/
    /*border: black 1px solid;*/
    /*vertical-align: middle;*/
    display: flex;
    /*padding-left: 2.5em;*/
    /*flex-direction: column;*/
    /*justify-content: right;*/
}

.education--link {
    display: flex;
    align-items: center;
    justify-content: right;
    /*margin-bottom: .4em;*/
    /*margin: 0 0 0 1em;*/
    /*border: red 1px solid;*/
}

.education--link--logo {
    max-width: 1em;
    margin-right: 1em;
}

.education--link--caption {
    margin-right: .5em;
}

.certificate--container {
    display: flex;
    justify-content: space-between;
    /*padding-left: 3.5em;*/
}

.certificate {
    max-width: 2.5rem;
}

td {
    /*padding-right: 5%;*/
    /*width: 65%;*/
}

/*td+td {*/
/*    width: auto;*/
/*}*/

.rating--table {

}

.rating--row {
    display: flex;
    justify-content: space-between;
}

.rating--row div {
    width: 5em;
}

.rating {
    width: 1em;
    display: flex;
}

.rating--circle {
    width: 1em;
    /*filter: invert(0.9);*/
    filter: invert(100%) sepia(11%) saturate(1662%) hue-rotate(341deg) brightness(87%) contrast(95%);
}

.circle--white {
    transform: scale(0.85);

}

/* Carousel */

.carousel {
    position: relative;
    width: 300%;
    right: 100%;
    margin: 0;
}

.slick-slide > div {
    padding: 0 -50px;
    margin: 0 -20px;
    height: 100%
}

.slick-track {
    display: flex !important;
    align-items: center;
}

.slick-slide {
    width: 70%;
}

.slick-list {
    margin: 0 70px;
}

.carousel--item {
    text-align: center;
    margin: 0;
    padding: 0;
    transform: scale(0.2);
    transition: transform 300ms;
    opacity: 0.5;
    outline-style: none;
}

.active--item {
    -webkit-transition: transform 1.5s ease-in-out;
    transform: scale(1);
    opacity: 1;
}

.carousel--item h4 {
    margin: .8em;
}

.carousel--item h4:hover {
    text-decoration: underline;
}

.carousel--item--preview--cont {
    position: relative;
}

.carousel--item--preview {
    filter: drop-shadow(0 0 1.5rem transparent);
    transition: filter .3s ease-in-out;
}

.carousel--item--shadow {
    filter: blur(.5rem);
    z-index: -1;
    position: absolute;
    top: 2.8em;
    left: 0;
}

.carousel--item:hover .carousel--item--preview {
    filter: drop-shadow(0 0 1.5rem lightgrey);
    transition: filter .5s ease-in-out;
}

.carousel--item img {
    width: 100%;
}

.carousel--item p {
    margin-top: 1.3em;
    margin-bottom: 1em;
}

.carousel--item ul {
    text-align: left;
}

.item--description {
    font-weight: 700;
    color: #d4d7c5 !important;
    text-align: center;
    margin-bottom: 0.5em !important;
    font-size: 1.2em;
}

.item--caption {
    text-align: center;
    margin-top: 0 !important;
    margin-bottom: 1.5em !important;
}

.projects--title {
    text-align: center;
    /*margin-top: 10%;*/
}

.item--buttons--container {
    display: flex;
    justify-content: center;
}

.carousel--button {
    width: 40%;
    margin: 0 0.7em;
}

/* footer */

.soc--icons {
    background: #161619;
    text-align: center;
    border-radius: 0 0 15px 15px;
    padding: 1rem;
    /*height: 1.5625rem;*/
}

.soc--icons img:hover {
    transform: scale(1.3);
    transition: transform 0.3s ease-in-out;
}

/* Light theme */

.light {
    background: url("../src/images/walper-inverted2.png");
}

.light .app{
    background-color: #F5F5F5;
    filter: drop-shadow(0 0 1rem darkgray);
}

.light .navbar--theme--icon {
    filter: invert(0%);
}

.light .navbar--github--icon {
    filter: invert(0%);
}

.light .occupation {
    color: #F47D27;
}

.light .project--button {
    background: #ffffff;
    border: 1px solid #D1D5DB;
    box-shadow: 0 1px 2px 0 #0000000D;
}

.light .cv--button {
    background: #297FE5;
    box-shadow: 0 1px 2px 0 #0000000D;
}

.light .cv--button {
    color: white;
}

.light .resume--icon {
    filter: invert(100%);
    width: .95em
}

.light p,
.light li,
.light table {
    color: #4A4E74;
}

.light h1,
.light h2,
.light h3,
.light h4 {
    color: #2B283A;
}

.light .education--link--logo {
    filter: invert(0);
}

.light .education--name {
    color: #2B283A;
}

.light .rating--circle {
    filter: invert(0.1);
}

.light .carousel--item h4:hover {
    text-decoration: underline;
}

.light .carousel--item:hover .carousel--item--preview {
    filter: drop-shadow(0 0 1rem darkgray);
    transition: filter .5s;
}

.light .carousel--item:hover .carousel--item--shadow {
    filter: blur(0);
    transition: filter .5s;
}

.light .carousel--item--shadow {
    filter: blur(.75rem);
    transition: filter .5s;
}

.light .item--description {
    color: #2B283A !important;
}

.light .soc--icons {
    background: #D5D4D8;
}


@media (min-width: 800px) {

    h3, h4 {
        font-size: 1.3rem;

    }

    .projects .h3, h4 {
        font-size: 1rem;
    }

    p, li, table {
        font-size: 1rem;
        line-height: 1.5rem;
    }

    p {

    }

    .container {
        padding-top: 7%;
    }

    .main {
        padding-left: 10%;
        width: 35%;
    }

    .app {
        position: relative;
    }

    .navbar--theme--icon {
        width: 53%
    }

    .navbar--github--icon {
        width: 50%;
        transform: scale(1);
    }

    .navbar--linkedin--icon {
        width: 46%;
        margin-left: 4%;
        transform: scale(1);
    }

    .navbar--mail--icon {
        width: 53%;
    }

    .avatar {
        border-radius: 50% 15px 50% 50%;
        position: absolute;
        left: 55%;
        top: 0;
        width: 45%;
    }

    .light .avatar {
        filter: drop-shadow(0 0 1rem darkgray);
    }

    .main--info {
        text-align: left !important;
    }

    .name {
        font-size: 3rem;
        max-width: 200px !important;
    }

    .occupation {
        font-size: 1.2rem !important;
        margin: 0 0 3em 0;
    }

    .buttons {
        padding: 0 0 !important;
        margin-bottom: 0;
    }

    .button {
        font-size: 0.875rem;
    }

    .body {
        display: grid;
        grid-template-columns: 45% 45%;
        column-gap: 10%;
        margin-top: 0;
    }

    .text--section {
        margin-bottom: 2.7rem;
    }

    td:nth-child(2) {
        width: 40%;
    }

    .about {
        grid-column: 1;
        grid-row: 1 / 3;
    }

    .education--history {
        grid-column: 1;
        grid-row: 3 / 9;
    }

    .pro {
        grid-column: 2;
        grid-row: 2 / 4;
    }

    .p--langs {
        grid-column: 2;
        grid-row: 4 / 6;
    }

    .projects {
        grid-column: 1 / 3;
        grid-row: 11 / 15;
        width: 50%;
        padding-left: 25%;
        margin-bottom: 0;
    }

    .soft--skills {
        grid-column: 2;
        grid-row: 6 / 7;
    }

    .location {
        grid-column: 2;
        grid-row: 7 / 8;
    }

    .langs {
        grid-column: 2;
        grid-row: 8 / 9;
    }

    .interests {
        grid-column: 1;
        grid-row: 9 / 10;
    }

    .soc--icons {
        height: 1.5625rem;
    }

}
